<template>
  <div class="home">
    <el-card class="outCard">
      <el-form>
        <el-col :span="20">
          <el-input v-model="input" @keyup="add" placeholder="输入添加事项内容"></el-input>
        </el-col>
        <el-button type="primary" @click="add">添加</el-button>
      </el-form>
      <br/>
      <el-card>
        <div slot="header" class="clearfix">
          <span>未完成</span>
        </div>
        <div v-for="(item,key) in list" :key="key" class="text item">
          <template v-if="!item.check">
            <input type="checkbox" v-model="item.check" />
            {{item.title}}
            <el-button type="danger" plain @click="del(key)">删除</el-button>
          </template>
        </div>
      </el-card>
      <el-card>
        <div slot="header" class="clearfix">
          <span>已完成</span>
        </div>
        <div v-for="(item,key) in list" :key="key" class="text item">
          <template v-if="item.check">
            <input type="checkbox" v-model="item.check" />
            {{item.title}}
            <el-button type="danger" plain @click="del(key)">删除</el-button>
          </template>
        </div>
      </el-card>
    </el-card>
  </div>
</template>

<script>
// @ is an alias to /src

export default {
  data() {
    return {
      input: "",
      list: [
        {
          title: "PTA做完了吗",
          check: true
        },
        {
          title: "代码写完了吗",
          check: false
        }
      ]
    };
  },
  methods: {
    add() {
      if (this.input != "") {
        this.list.push({
          title: this.input,
          check: false
        });
      }
      this.input = "";
    },
    del(val) {
      this.list.splice(val, 1);
    }
  }
};
</script>
<style scoped>
.outCard {
  width: 50vw;
  margin: 0 auto;
}
.text {
  font-size: 16px;
}

.item {
  margin-bottom: 18px;
}
.clearfix span{
  font-size: 22px;
}
.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}
.clearfix:after {
  clear: both;
}
